.container {
    min-height: 100vh;
    width: 100%;

    display: grid;
    grid-template-rows: minmax(min-content, 6rem) 1fr minmax(min-content, 3rem);

    // background: linear-gradient(to bottom right, rgb(12, 90, 60), rgb(50, 12, 170));
    background: linear-gradient(to left bottom, rgba(50, 90, 152), rgb(4, 126, 55));
    color: white;
}

.section-main {
    max-width: 120rem;
    min-width: 60rem;
    margin: 0 auto;
}
